* {
  margin: 0;
  color: white;
  box-sizing: border-box;
}
body {
  background-color: black;
  /* font-family:  "Apple SD Gothic Neo", "Malgun Gothic", sans-serif;  */
}

a {
  text-decoration: none;
}
ul,
ol {
  list-style: none;
}
.container {
  align-items: center;
  justify-content: center;
  padding: 20px;
}
/* 메인화면 애니메이션 */
@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(calc(-250px * 7));
  }
}
.slider {
  margin: auto;
  overflow: hidden;
  position: relative;
  width: auto;
}
.slide {
  margin-right: 50px;
}

.slider .slide-track {
  animation: scroll 40s linear infinite;
  display: flex;
  width: calc(250px * 14);
}

.bb{
  color:white;
}


